<template>
  <div class="home">
    <!-- <div v-if="bannerList.length"> -->
    <Slider :imgList="bannerList" />
    <!-- </div> -->

    <MainItem class="product-part" title="" :showMore="false" @handleMore="handleMoreCompany">
      <!-- <div class="product-intro wow fadeInUp">
        <p>{{productIntro}}</p>
      </div>-->

      <div v-if="seriesList.length" class="product-swiper wow fadeInUp">
        <Swiper
          :listLength="seriesList.length"
          :swiperOptions="swiperOptionsInWeb"
          :showPoint="true"
          class="show-in-web"
          @handleIndexChange="handleIndexChange"
        >
          <swiper-slide v-for="(item, index) in seriesList" :key="index">
            <product-item :key="index" :msg="item" @click.native="handleSeries(item)"></product-item>
          </swiper-slide>
        </Swiper>

        <Swiper
          :listLength="seriesList.length"
          :swiperOptions="swiperOptionsInTop"
          :showPoint="true"
          class="show-in-laptop"
          @handleIndexChange="handleIndexChange"
        >
          <swiper-slide v-for="(item, index) in seriesList" :key="index">
            <product-item :key="index" :msg="item" @click.native="handleSeries(item)"></product-item>
          </swiper-slide>
        </Swiper>

        <Swiper
          :listLength="seriesList.length"
          :swiperOptions="swiperOptionsInMobile"
          :showPoint="true"
          class="show-in-Mobile"
          @handleIndexChange="handleIndexChange"
        >
          <swiper-slide v-for="(item, index) in seriesList" :key="index">
            <product-item :key="index" :msg="item" @click.native="handleSeries(item)"></product-item>
          </swiper-slide>
        </Swiper>

        <Swiper
          :listLength="seriesList.length"
          :swiperOptions="swiperOptionsInPhone"
          :showPoint="true"
          class="show-in-phone"
          @handleIndexChange="handleIndexChange"
        >
          <swiper-slide v-for="(item, index) in seriesList" :key="index">
            <product-item :key="index" :msg="item" @click.native="handleSeries(item)"></product-item>
          </swiper-slide>
        </Swiper>
      </div>

      <a
        class="product-more wow fadeInUp"
        href="javascript:void(0)"
        @click.stop.prevent="handleMoreProduct"
      ></a>
    </MainItem>

    <Adver url="https://dev-api.jjh9999.com/ruiqi/images/1587960956370.jpg" />

    <MainItem v-if="news.length" class="news-part" title="">
      <News :newsList="news" />
    </MainItem>

    <MainItem class="image-part" title="">
      <div class="image-part_item border_default">
        <span class="image-part_play" @click="handleVideoChange(true)"></span>
      </div>
      <div class="introduce-box" :class="{show:showVideo}" flex="main:center cross:center">
        <div class="video-box_wrap">
          <aside class="video-box fs16">
            <label
              for="introduce"
              @click="handleVideoChange(false)"
              class="introduce-label close-video_btn"
            >
              <img class="close" src="~@assets/modal-ico-close.svg" alt="x" />
            </label>
            <video controls ref="video" class="introduce_video" name="media">
              <source
                src="https://1500000261.vod2.myqcloud.com/438167b9vodtranscq1500000261/81bcebc25285890802615717012/v.f100020.mp4"
