              />
            </video>
          </aside>
        </div>
      </div>
    </MainItem>

    <MainItem v-if="cultureMsg.list.length" class="culture-part" title="">
      <Culture :msg="cultureMsg" />
    </MainItem>

    <a-modal
      :width="$device.phone ? '100vw' : '60vw'"
      :visible="previewVisible"
      :footer="null"
      @cancel="handleCancel"
    >
      <img alt="example" style="width: 100%" :src="previewImage" />
    </a-modal>
    <!-- <Test/> -->
