</template>

<script>
// import Carousel from '@/components/carousel'
import Slider from '@/components/slider'
import MainItem from '@/components/mainItem'
import Swiper from '@/components/swiper'
import ProductItem from './component/productItem'
import { SwiperSlide } from 'vue-awesome-swiper'
import Adver from './component/adver'
import News from './component/news.vue'
import Culture from './component/culture.vue'
import { homeData } from '@/js/api/homePage.js'
// import Test from './component/test'
import { WOW } from 'wowjs'
import { Modal } from 'ant-design-vue'

let baseOption = {
  slidesPerView: 1,
  // spaceBetween: 1,
  freeMode: true,
  autoplay: 3000,
  speed: 1000,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
    type: 'bullets'
  }
}

export default {
  name: 'Home',
  data() {
    return {
      productList: [],
      seriesList: [],
      adver: {},
      news: [],
      showVideo: false,
      swiperOptionsInWeb: {
        ...baseOption,
        speed: 200,
        slidesPerView: 3,
        slidesPerColumn: 2,
        spaceBetween: 24,
        observer: true,
        observeParents: true
      },
      swiperOptionsInMobile: {
        ...baseOption,
        spaceBetween: 12,
        speed: 200,
        pagination: {
          ...baseOption.pagination,
          el: '.swiper-pagination-mobile'
        }
      },
      swiperOptionsInTop: {
        ...baseOption,
        spaceBetween: 16,
        slidesPerView: 3,
        slidesPerColumn: 2,
        speed: 200,
        pagination: {
          ...baseOption.pagination,
          el: '.swiper-pagination-top'
        }
      },
      swiperOptionsInPhone: {
        ...baseOption,
        speed: 200,
        pagination: {
          ...baseOption.pagination,
          el: '.swiper-pagination-phone'
        }
      },
      companyList: [],
      productIntro: '',
      bannerList: [],
      previewVisible: false,
      previewImage: '',
      cultureList: [],
      cultureMsg: {
        content: '',
        list: []
      }
    }
  },
  components: {
    // Carousel,
    Slider,
    MainItem,
    Swiper,
    SwiperSlide,
    ProductItem,
    News,
    // Advertisement,
    // NewsItem,
    // CompanyItem,
    Adver,
    // Category,
    Culture,
    [Modal.name]: Modal
    // Test
  },
  created() {
    homeData({}, res => {
      let data = res.data
      this.bannerList = data.bannerList.map(item => {
        return {
          src: item.imgUrl
        }
      })
      this.productList = res.data.newGoodsList
      this.seriesList = res.data.seriesList
      this.productIntro = this.productList[0].desc
      this.news = data.newsList.map(newsItem => {
        let updateAt = newsItem.updateAt.replace(' ', '-')
        let createAt = newsItem.createAt.replace(' ', '-')
        updateAt = updateAt.split('-')
        createAt = createAt.split('-')
        let content = newsItem.content
        let comTemp = document.createElement('div')
        comTemp.innerHTML = content
        content = comTemp.innerText
        return {
          ...newsItem,
          updateAt: +updateAt[1] + '.' + updateAt[2],
          createAt: +createAt[1] + '.' + createAt[2],
          content
        }
      })
      this.cultureList = data.cultureList
      let firstCultureContent = ''
      if (this.cultureList.length) {
        let box = document.createElement('div')
        box.innerHTML = this.cultureList[0].content
        firstCultureContent = box.innerText
      }
      this.cultureMsg = {
        content: firstCultureContent,
        list: data.corporateImage
      }
      this.$nextTick(() => {
        new WOW().init()
      })
    })
  },
  mounted() {
    new WOW().init()
  },
  methods: {
    handleVideoChange(play) {
      this.showVideo = play
      if (play) {
        setTimeout(() => {
          this.$refs.video.play()
        }, 200)
      } else {
        setTimeout(() => {
          this.$refs.video.pause()
        }, 200)
      }
    },
    // handleBtn() {
    //   this.swiperOptionsInWeb = {
    //     ...baseOption,
    //     speed: 200,
    //     slidesPerView: 3,
    //     spaceBetween: 46
    //   }
    // },
    handleMoreProduct() {
      this.$router.push(`/category/${this.productList[0].category}`)
    },
    handleMoreNews() {},
    handleMoreCompany() {},
    handleSeries(item) {
      // console.log(item)
      this.$router.push(`/series/${item.category._id}/${item._id}`)
    },
    handleProduct(item) {
      this.previewVisible = true
      this.previewImage = item.imgUrlList && item.imgUrlList[0]
    },
    handleCancel() {
      this.previewVisible = false
    },
    handleIndexChange(index) {
      this.productIntro = this.productList[index].desc
    }
  }
}
</script>

