<template>
  <div flex="cross:center main:justify" class="tab_wrap-box" ref="slide">
    <template v-for="(item, index) in items">
      <div class="tab_wrap-item" :key="index">
        <button class="mdc-btn mdc-btn--tab" :class="{active: nowActive === index}"
                @click.stop="handleClick(index, item, $event)">
          <span>{{ item.label }}</span>
        </button>
      </div>
    </template>
    <!--<div class="tab_wrap-line" ref="line"></div>-->
  </div>
</template>

<script>
export default {
  name: 'TabBar',
  created () {
    this.nowActive = this.active
  },
  mounted () {
    this.$nextTick(() => {
      // console.log('', this.$fn.getCss(document.querySelector('.tab_wrap-item'), 'width'))
      if (this.$device.phone) {
        return
      }
      const width = parseInt(this.$fn.getCss(document.querySelector('.tab_wrap-item'), 'width'))
      const right = parseInt(this.$fn.getCss(document.querySelector('.tab_wrap-item'), 'marginRight'))
      this.$refs.slide.style.width = width * this.items.length + right * (this.items.length - 1)  + 'px'
    })
  },
  data () {
    return {
      nowActive: -1
    }
  },
  watch: {
    active (val) {
      this.nowActive = val
      // if (val > -1) {
      //   const evt = {
      //     target: document.querySelectorAll('.mdc-btn')[val]
      //   }
      //   this.$nextTick(() => {
      //     // this.computedActive(evt)
      //   })
      // }
    }
  },
  methods: {
    handleClick (index, item, evt) {
      this.nowActive = index
      // this.computedActive(evt)
      this.$emit('change', index, item)
    },
    computedActive (evt) {
      var Button = evt.target
      if (Button.tagName !== 'BUTTON') {
        Button = Button.parentElement
      }
      this.$refs.line.style.width = this.$fn.getCss(Button, 'width')

      var left = Button.getBoundingClientRect().left

      const wrap = parseFloat(this.$fn.getCss(this.$refs.slide.parentElement, 'paddingLeft'))
      // 0.5=1.5/3 letter space
      this.$refs.slide.style.setProperty('--left', `${left - wrap - 0.5}px`)
    }
  },
  props: {
    items: Array,
    active: Number
  },
  components: {}
}
</script>

