          margin: 0 auto;
        }
      })
    }
    &-line {
      position: absolute;
      bottom: 0;
      left: var(--left);
      height: 3px;
      background: #333333;
      transition: all .3s;
      border-radius: 3px;
    }

  }

  .tab_wrap-item {
    text-align: center;
    transition: color .3s ease;
    padding: 0;
    &:not(:last-child) {
      margin-right: 6em;
    }
    .phone({
      &:not(:last-child) {
        margin-right: 0;
      }
    });
    &.active {
      color: #333;
    }

    position: relative;

  }

  .tab_sup {
