<template>
  <div>
    <news-top-img :mainBg="mainBg" />
    <div class="newsDetail common-wrap">
      <div class="loading" v-if="isLoading">
        <a-spin>
          <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #dab866" spin />
        </a-spin>
      </div>
      <div v-else class="content-wrapper">
        <div class="top">
          <!-- <div class="time showPoint">{{detail.updateAt | filterTime}}</div> -->
          <div class="title showPoint">{{detail.title}}</div>
        </div>
        <div class="content" v-html="detail.content"></div>
        <div class="line"></div>
        <div class="bottom clearfix">
          <!-- <div class="subling">
          <p class="pre">
            <span></span>
            <a
              @click.stop.prevent="handleBefore"
              :disabled="!before"
              href="#"
              class="showPoint"
            >{{(before && before.title) || '' }}</a>
          </p>
          <p class="after">
            <span></span>
            <a
              @click.stop.prevent="handleAfter"
              :disabled="!after"
              href="#"
              class="showPoint"
            >{{(after && after.title) || '' }}</a>
          </p>
          </div>-->
          <a class="go-list" href="#" @click.stop.prevent="handleReturn"></a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getNewsDetail } from '@/js/api/homePage.js'
import NewsTopImg from '@/components/newsTopImg'
import { Spin, Icon } from 'ant-design-vue'
export default {
  name: 'newsDetail',
  data() {
    return {
      detail: {},
      before: {},
      after: {},
      isLoading: true,
      mainBg: ''
    }
  },
  components: {
    [Spin.name]: Spin,
    [Icon.name]: Icon,
    NewsTopImg
  },
  filters: {
    filterTime(val) {
      return val.slice(0, 10)
    }
  },
  created() {
    let id = this.$route.params.id
    getNewsDetail(
      {
        articleId: id
      },
      res => {
        let data = res.data
        this.detail = data.article
        this.before = data.before
        this.after = data.after
        this.isLoading = false
        this.mainBg = this.detail.mainImgUrlList[0]
      }
    )
  },
  methods: {
    handleBefore() {
      this.$router.push(`/newsDetail/${this.before._id}`)
    },
    handleAfter() {
      this.$router.push(`/newsDetail/${this.after._id}`)
    },
    handleReturn() {
      this.$router.push(`/webNews/${this.detail.group._id}`)
    }
  }
}
</script>

