<template>
  <div class="news clearfix" v-if="newsList.length">
    <!-- <transition-group tag="div" name="slide"> -->
      <div class="item-wrapper border_default" v-for="(item, index) in newsList" :key="index" :class="{first: index === currentIndex, second: index === currentIndex + 1, third: index === currentIndex + 2}">
         <div
          class="item base_img_bg wow zoomIn"
          @click="handleNewsDetail(item._id)"
          v-show="index === currentIndex || index === currentIndex + 1 || index === currentIndex + 2"
          :style="{backgroundImage: `url(${item.mainImgUrlList && item.mainImgUrlList[0]})`}"
        >
        </div>
        <time v-if="index !== currentIndex" class="time">{{item.updateAt}}</time>
        <div v-if="index !== currentIndex" class="desc showPoint">{{item.title}}</div>
      </div>

    <!-- </transition-group> -->
    <div class="desc-wrapper wow fadeInUp" flex="dir:top">
      <div class="time">{{currentItem.updateAt}}</div>
      <div class="name showPoint">{{currentItem.title}}</div>
      <p class="desc" flex-box="1">{{ currentItem.sTitle }}</p>
      <a class="get-more" href="javscript:void(0)" @click.stop.prevent="handleMoreNews(false)"></a>
    </div>

    <a-icon type="left" class="left-icon icon" :disabled="currentIndex ===  0" @click="handleLeft" />
    <a-icon type="right" class="right-icon icon" :disabled="currentIndex === newsList.length -1" @click="handleRight"/>
  </div>
</template>

<script>
import { Icon } from 'ant-design-vue'
export default {
  name: 'news',
  props: {
    newsList: {
      type: Array,
      default: function () {
        return [
        ]
      }
    }
  },
  components: {
    [Icon.name]: Icon
  },
  data () {
    return {
      currentIndex: 0,
      currentItem: ''
    }
  },
  methods: {
    handleLeft () {
      if (this.currentIndex === 0) {
        return
      }
      this.currentIndex--
      this.currentItem = this.newsList[this.currentIndex]

    },
    handleRight () {
      if (this.currentIndex === this.newsList.length - 1) {
        return
      }
      this.currentIndex++
      this.currentItem = this.newsList[this.currentIndex]
    },
    handleMoreNews () {
      // 
      // this.$router.push(`/newsDetail/${this.newsList[this.currentIndex]._id}`)
      // 
      this.$router.push(`/webNews/${this.newsList[this.currentIndex].group}`)
    },
    handleNewsDetail(url){
      this.$router.push(`/newsDetail/${url}`)
    },
  },
  watch: {
    newsList: {
      immediate: true,
      handler (val) {
        this.currentItem = val[0]
      }
    }
  }
}
</script>

