<template>
  <div class="w-category" ref="category">
    <header class="w-category-bg" :data-bg="bigImgUrl" />
    <main class="w-category-main">
      <div class="w-tab-x">
        <Tabbar :items="items" class="w-category-tab" @change="handleChange" :active="active" />
      </div>

      <section class="w-category-section" ref="section">
        <template v-for="(item, idx) in series">
          <div
            :key="idx"
            class="w-series wow fadeInUp"
            flex="cross:center"
            data-wow-duration="1200"
          >
            <seriesImgItem
              v-if="idx % 2 === 0 || $device.phone"
              :idx="idx"
              :item="item"
              @click="handleClick"
            />
            <swipeItem :idx="idx" :item="item" v-if="item.goodsList && item.goodsList.length" />
            <seriesImgItem
              v-if="idx % 2 === 1 && !$device.phone"
              :idx="idx"
              :item="item"
              @click="handleClick"
            />
          </div>
        </template>
      </section>
    </main>
  </div>
</template>

<script>
import Tabbar from '@components/bossTabbar'
import { Carousel, Icon } from 'ant-design-vue'
import { listCategory, listSeries } from '../../../js/api/good'
import seriesImgItem from './seriesImgItem'
import swipeItem from './swipeItem'
import { WOW } from 'wowjs'
export default {
  name: 'RCategory',
  components: {
    Tabbar,
    [Carousel.name]: Carousel,
    [seriesImgItem.name]: seriesImgItem,
    [swipeItem.name]: swipeItem,
    [Icon.name]: Icon
  },
  created() {
    this.init()
  },
  mounted() {},
  data() {
    return {
      items: [
        { label: '' },
        { label: '' },
        { label: 'K' },
        { label: '' },
        { label: '' },
        { label: '' }
      ],
      series: [],
      bigImgUrl: '',
      scrollHandler: null,
      scrolled: true,
      active: -1,
      boxes: [],
      wow: null
    }
  },

  methods: {
    // 
    handleClick(item) {
      this.$router.push(`/series/${item.category._id}/${item._id}`)
    },
    // tab
    handleChange(index, item) {
      this.initSeries(item.value)

      // this.$dom.moveTop(window.document.documentElement, () => {})
    },
    // 
    init() {
      listCategory(
        {
          page: 1,
          pageSize: 200
        },
        res => {
          this.items = res.data.list.map(item => this.checkCategory(item))
          const id = this.$route.params.id

          this.active = res.data.list.findIndex(item => item._id === id)
          this.initSeries(id)
        }
      )
    },
    checkCategory(data) {
      return {
        label: data.name,
        value: data._id
      }
    },
    // 
    initSeries(value) {
      listSeries(
        {
          categoryId: value,
          page: 1,
          pageSize: 200
        },
        res => {
          res.data.list.forEach(item => {
            item.active = 0
          })
          this.series = res.data.list
          // const bigImgUrl = res.data.list.map(item => item.bigImgUrl).filter(_ => _)
          // this.bigImgUrl = bigImgUrl[0]
          // initdom
          this.$nextTick(() => {
            this.boxes = [].slice.call(this.$refs.section.children)
            this.boxes.forEach(item => {
              if (
                item.classList.contains('is-visble') ||
                item.classList.contains('full-visble')
              ) {
                item.classList.remove('is-visible')
                item.classList.remove('full-visible')
              }
            })
            if (window.document.documentElement.scrollTop !== 0) {
              // this.scrollCallBack(true)
            }
            this.initScroll()
          })
        }
      )
    },
    // 
    // 
    initScroll() {
      // const _this = this
      // window.addEventListener('scroll', _this.scrollHandler = function () {
      //   _this.scrollCallBack()
      // })
      if (this.wow) {
        this.wow.sync()
        return
      }
      this.wow = new WOW()
      this.wow.init()
    },
    scrollCallBack(init) {
      let box
      let _i, _len, _ref
      _ref = this.boxes
      for (_i = 0, _len = _ref.length; _i < _len; _i++) {
        box = _ref[_i]
        if (!box) {
          continue
        }
        if (this.$dom.isVisible(box, 20)) {
          this.show(box, init)
          this.boxes.splice(_i, 1)
          continue
        }
      }
      if (!this.boxes.length) {
        return this.stop()
      }
    },
    show(dom, init) {
      if (init) {
        dom.classList.add('full-visible')
        return
      }
      dom.classList.add('is-visible')
    },
    stop() {
      window.removeEventListener('scroll', this.scrollHandler, false)
    }
  },
  props: {}
  // computed: {
  //   bgStyle () {
  //     return `background: url('${this.bigImgUrl}') center/cover no-repeat fixed`
  //   }
  // }
}
</script>

