<template>
  <div
    flex-box="1"
    :class="[idx % 2 === 0 ? 'ml24' : 'mr24', $device.phone ? 'm0' : '']"
    class="w-carousel"
  >
    <div class="wrapper" v-if="ready" flex-box="1" ref="wrapper">
      <swiper :options="swiperOptions">
        <template v-for="(good, gx) in item.goodsList" >
          <swiper-slide
            class="w-carousel-div"
            v-for="(img, index) in good.imgUrlList"
            :key="`${gx}_${index}_${Math.random()}`"
          >
            <img :src="img" alt="" class="w-carousel-img"  @click.stop="handlePrevie(img)">
            <!--<vue-photo-zoom-pro-->
              <!--:disabled="true"-->
              <!--:url="img"-->
              <!--class="w-carousel-img"-->
              <!--:zoomer-style="{backgroundColor: 'transparent'}"-->
            <!--/>-->
          </swiper-slide>
        </template>

        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
      <div
        class="w-carousel-btn prev"
        :class="[swiperOptions.navigation.prevEl.split('.')[1]]"
        slot="button-prev"
      >
        <span class="w-btn-icon" flex="cross:center main:center">
          <a-icon type="left" style="font-size: 16px" />
        </span>
      </div>
      <div
        class="w-carousel-btn next"
        :class="[swiperOptions.navigation.nextEl.split('.')[1]]"
        slot="button-next"
      >
        <span class="w-btn-icon" flex="cross:center main:center">
          <a-icon type="right" style="ont-size: 16px" />
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'ant-design-vue'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import bus from '@bus'
import 'swiper/css/swiper.css'

export default {
  name: 'swipeItem',
  components: {
    [Icon.name]: Icon,
    Swiper,
    SwiperSlide
  },
  created() {
    this.swiperOptions.navigation.prevEl = `.btn-${this.item._id}-prev`
    this.swiperOptions.navigation.nextEl = `.btn-${this.item._id}-next`
    this.ready = true
    if (this.$device.phone) {
      this.swiperOptions.slidesPerView = 2
      this.swiperOptions.slidesPerColumn = 1
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.judgePadding()
    })
  },
  data() {
    return {
      ready: false,
      swiperOptions: {
        slidesPerView: 3,
        slidesPerColumn: 3,
        spaceBetween: 0,
        freeMode: true,
        speed: 500,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          type: 'bullets'
        },
        navigation: {}
        // Some Swiper option/callback...
      }
    }
  },
  methods: {
    handlePrevie(item) {
      bus.$emit('showImgModal', item)
    },
    judgePadding() {
      const wrapper = this.$refs.wrapper
      const first = wrapper.firstElementChild
      const parent = wrapper.parentElement

      const pH = parent.offsetHeight
      const wH = wrapper.offsetHeight
      const pad = (pH - wH) / 2
      first.style.paddingTop = pad + 'px'
      first.style.paddingBottom = pad + 'px'
    }
  },
  props: {
    idx: Number,
    item: Object
  },
  computed: {}
}
</script>

