<template>
  <div class="swiper_div">
    <div class="wrapper" :class="isHome?'home-swiper':''">
      <swiper ref="mySwiper" :options="swiperOptions" class="swiper-no-swiping" @slideChange="change" :autoUpdate="true">
        <slot></slot>
        <div v-if="showPoint" class="swiper-pagination" slot="pagination"></div>
        <div v-if="showPoint" class="swiper-pagination-mobile" slot="pagination"></div>
        <div v-if="showPoint" class="swiper-pagination-top" slot="pagination"></div>
        <div v-if="showPoint" class="swiper-pagination-phone" slot="pagination"></div>
      </swiper>
      <!-- <div class="swiper-button-prev-1" slot="button-prev"></div>
      <div class="swiper-button-next-1" slot="button-next"></div> -->
<!--
      <div v-if="type === 'mobile'" class="swiper-button-prev" slot="button-prev"></div>
      <div v-if="type === 'mobile'" class="swiper-button-next" slot="button-next"></div>

      <div v-if="type === 'top'" class="swiper-button-prev" slot="button-prev"></div>
      <div v-if="type === 'top'" class="swiper-button-next" slot="button-next"></div>

      <div v-if="type === 'phone'" class="swiper-button-prev" slot="button-prev"></div>
      <div v-if="type === 'phone'" class="swiper-button-next" slot="button-next"></div> -->
    </div>
    <svg-icon v-if="activeIndex === 0" icon-class="left_disabled_icon" class="left-icon--disabled icon"></svg-icon>
    <svg-icon v-else icon-class="left_normal_icon" class="left-icon icon" @click.stop.prevent.native="handle('pre')"></svg-icon>
    <svg-icon v-if="activeIndex + swiperOptions.slidesPerView === listLength" icon-class="right_disabled_icon" class="right-icon--disabled icon"></svg-icon>
    <svg-icon v-else icon-class="right_normal_icon" class="right-icon icon" @click.stop.prevent.native="handle('after')"></svg-icon>
  </div>
</template>

<script>
import { Swiper, directive } from 'vue-awesome-swiper'
import '@/assets/icons/left_disabled_icon.svg'
import '@/assets/icons/left_normal_icon.svg'
import '@/assets/icons/right_disabled_icon.svg'
import '@/assets/icons/right_normal_icon.svg'
// import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
// import ProductItem from '@/views/web/home/component/productItem'
export default {
  name: 'Home',
  data () {
    return {
      activeIndex: 0
    }
  },
  props: {
    showPoint: {
      type: Boolean,
      default: true
    },
    isHome: {
      type: Boolean,
      default: true
    },
    swiperOptions: {
      type: Object,
    },
    type: {
      type: String,
      default: 'web'
    },
    listLength: {
      type: Number
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.$swiper
    }
  },
  methods: {
    change () {
      if (this.activeIndex === this.swiper.activeIndex) {
        return
      }
      this.activeIndex = this.swiper.activeIndex
      this.$emit('handleIndexChange', this.activeIndex)
    },
    handle (type) {
      // console.log('this.activeIndex', this.activeIndex)
      if (type === 'pre') {
        this.swiper.slideTo(this.activeIndex - 1,500,false)
      } else {
        this.swiper.slideTo(this.activeIndex + 1,500,false)
      }
    }
  },
  components: {
    Swiper,
  },

  directives: {
    swiper: directive
  },
}
</script>

<style scoped lang="less">
@import '~@/style/tools/_mediaqueries';
