      color: #333;
    }
    // &[aria-disabled='true'] {
    //   border-color: #eee;
    //   &:after {
    //     color: #ddd;
    //   }
    // }
  }
  .swiper-button-disabled {
    border: 1px solid rgba(238, 238, 238, 1);
  }
  .swiper-button-prev {
    left: -96px;
    .middletop({left: -56px;});
    .phone({left: 0;});
  }
  .swiper-button-next {
    right: -96px;
    .middletop({right: -56px;});
    .phone({right: 0;});
  }
  // .swiper-container {
  //   padding-bottom: 55px;
  // }
  .swiper-pagination {
    // width: 200px;
    // height: 200px;
    &.swiper-pagination-bullets{
      position: relative;
      bottom:0;
    }
    .swiper-pagination-bullet {
      display: inline-block;
      width: 24px !important;
      height: 2px !important;
      border-radius: 0;
      background-color: #333 !important;
      border: solid 3px #333;
    }
  }
  .icon {
    font-size: 34px;
    cursor: pointer;
  }
  .left-icon--disabled, .right-icon--disabled {
    // pointer-events: none;
    cursor: not-allowed;
  }
  .left-icon--disabled, .left-icon, .right-icon--disabled, .right-icon {
    position: absolute;
    top: 50%;
    z-index: 10;
    transform: translateY(-50%);
    user-select: none;
  }
  .left-icon--disabled, .left-icon {
    .monitor({left: -100px;});
    .laptop({left: -70px});
    .mobile({left: -50px});
    .phone({left: -4px});
  }
  .right-icon--disabled, .right-icon {
    .monitor({right: -100px;});
    .laptop({right: -70px});
    .mobile({right: -50px});
    .phone({right: -4px});
  }
}
</style>
