<template>
  <div class="w-login" flex="cross:center main:center">

    <div  class="login-container" >
      <a-form :form="form" @submit="handleSubmit">
        <a-form-item :validate-status="userNameError() ? 'error' : ''" :help="userNameError() || ''">
          <a-input
            class="login-input"
            autocomplete="off"
            v-decorator="[
          'userName',
          { rules: [{ required: true, message: '!' }] },
        ]"
            placeholder=""
          >
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-item>
        <a-form-item :validate-status="passwordError() ? 'error' : ''" :help="passwordError() || ''">
          <a-input
            class="login-input"
            autocomplete="off"
            v-decorator="[
          'password',
          { rules: [{ required: true, message: '!' }] },
        ]"
            type="password"
            placeholder=""
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-item>
        <a-form-item>
          <div class="submit">
            <a-button type="primary" html-type="submit" size="large">
              
            </a-button>
          </div>
        </a-form-item>
      </a-form>
    </div>

  </div>
</template>

<script>
import { Form, Icon, Input, Button } from 'ant-design-vue'

import { login } from '@js/api/base'
function hasErrors (fieldsError) {
  return Object.keys(fieldsError).some(field => fieldsError[field])
}
export default {
  name: 'Login',
  components: {
    [Form.name]: Form,
    [Form.Item.name]: Form.Item,
    [Input.name]: Input,
    [Button.name]: Button,
    [Icon.name]: Icon
  },
  data () {
    return {
      hasErrors,
      form: this.$form.createForm(this, { name: 'horizontal_login' })
    }
  },
  mounted () {
    this.$nextTick(() => {
      // To disabled submit button at the beginning.
      this.form.validateFields()
    })
  },
  methods: {
    // Only show error after a field is touched.
    userNameError () {
      const { getFieldError, isFieldTouched } = this.form
      return isFieldTouched('userName') && getFieldError('userName')
    },
    // Only show error after a field is touched.
    passwordError () {
      const { getFieldError, isFieldTouched } = this.form
      return isFieldTouched('password') && getFieldError('password')
    },
    handleSubmit (e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          console.log('Received values of form: ', values)


          login({
            account: values.userName || 'ruqiadmin',
            pwd: values.password || '123'
          }, res => {
            console.log(res)
            this.$cookies.setItem('jwt', res.data.jwtToken, 7 * 24 * 69 * 60, '')
            this.$router.replace('/boss')
          })
        }
      })
    }
  }
}
</script>
<style lang="less">
  .login-input {
    input.ant-input {
      color: #fff;
      background: transparent !important;
      border-color: rgba(255, 255, 255, .12);
    }
  }
</style>
<style lang="less" scoped>
  .w-login {
    height: 100%;
    background: url("https://dev-api.jjh9999.com/ruiqi/images/1587894297866.jpg") center no-repeat;
    background-size: cover;
  }
  .login-container {
    width: 480px;
    margin: auto;
  }
  .submit {
    button {
      width: 100%;
    }
  }
</style>
