<template>
  <div>
    <header class="w-detail-bg" ref="header" v-if="detail">
      <img :src="detail.bigImgUrl" alt="" ref="headerImg">
    </header>

    <div class="w-detail-c">

      <div class="w-product" v-if="detail">{{detail.name}}</div>
      <div class="w-product-desc" v-if="detail">{{detail.desc}}</div>

      <div class="w-detail-x">
        <img
          src="https://www.ganluchina.com/wp-content/uploads/2019/07/4ec5427f8722bd24aff242cb736d7b6.png"
          alt
        />
      </div>

      <section flex="main:justify cross:center wrap:wrap" class="w-detail-x" v-if="detail">
        <template v-for="(good, gx) in detail.goodsList">
          <div
            class="w-detail-div"
            v-for="(img, index) in good.imgUrlList"
            :key="`${gx}_${index}`"
            @click.stop="handlePrevie(img)"
          >
            <img class="w-detail-img" :src="img" alt />

            <div class="title">{{ good.title }}</div>
            <div class="desc">{{ good.desc }}</div>
          </div>
        </template>
      </section>

    </div>
    <a-modal :width="$device.phone ? '100vw' : '60vw'" :visible="previewVisible" :footer="null" @cancel="handleCancel" class="category-modal">
      <img alt style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import { listSeries } from '../../../js/api/good'
import { Modal } from 'ant-design-vue'
export default {
  name: 'categoryDetail',
  components: {
    [Modal.name]: Modal
  },
  created() {
    const category = this.$route.params.category
    const seriesId = this.$route.params.index
    this.init(category, seriesId)
  },
  mounted() {
    const _this = this
    window.addEventListener('resize', _this.resizeFun = function () {
      _this.$refs.headerImg.style.height = _this.$refs.header.offsetHeight + 'px'
    })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.resizeFun)
  },
  data() {
    return {
      oddFileList: [],
      detail: null,
      previewVisible: false,
      previewImage: '',
      resizeFun: null
    }
  },
  methods: {
    handleCancel() {
      this.previewVisible = false
    },
    handlePrevie(item) {
      this.previewImage = item
      this.previewVisible = true
    },
    // 
    init(value, seriesId) {
      listSeries(
        {
          categoryId: value,
          page: 1,
          pageSize: 200
        },
        res => {
          const oddFileList = []

          this.detail = res.data.list.find(_ => _._id === seriesId)
          this.detail.goodsList &&
            this.detail.goodsList.forEach(item => {
              oddFileList.push.apply(oddFileList, item.imgUrlList)
            })

          this.$nextTick(() => {
            this.$dom.moveTop(window.document.documentElement, () => {})
          })
          // this.oddFileList = oddFileList
        }
      )
    },
  },
  props: {},
  computed: {
    bgStyle() {
      if (this.detail) {
        return {
          backgroundImage: `url('${this.detail.bigImgUrl}')`,
        }
      }
    }
  },
  watch: {
    detail: {
      handler: function (val) {
        console.log(val)
        if (val) {
          this.$nextTick(() => {
            this.$refs.headerImg.style.height = this.$refs.header.offsetHeight + 'px'
          })
        }
      },
      deep: true,
      immediate: true
    }
  }
}
</script>

