<template>
  <div class="slider">
    <section class="home-section kenbarn">
      <div class="image-wrap">
        <div class="kenburn-overlay">
          <div class="intro-section text-center"></div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'slider',
  data () {
    return {
      flag: false
    }
  },
  props: {
    imgList: {
      type: Array
    }
  },
  mounted () {
    this.flag = true
  },
  methods: {
    setSlide (imgList) {
      $('.kenbarn').vegas({
        overlay: true,
        transitionDuration: 4000,
        delay: 10000,
        animation: 'random',
        animationDuration: 20000,
        slides: imgList
      })
    }
  },
  watch: {
    imgList (val) {
      if (val.length) {
        this.setSlide(val)
      }
    }
  }
}
</script>

<style scoped lang="less">
.slider {
  // margin-top: 80px;
}
.home-section {
  height: 100vh !important;
}
</style>
