<template>
  <div class="corporate common-wrap">
    <news-top-img />
    <div class="loading" v-if="isLoading">
      <a-spin >
        <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #dab866" spin />
      </a-spin>
    </div>
    <div v-else>
      <div class="list-wrapper" v-if="showList">
        <div class="title">{{title}}</div>
        <div v-if="list.length" class="lists">
          <a-row :gutter="[30,30]">
            <a-col :span="colSpan" v-for="(item, index) in list" :key="index">
              <Item :msg="item" @handleNewItem="handleItem(item)"/>
            </a-col>
          </a-row>
        </div>
        <div class="no-content" v-else>
          
        </div>
      </div>
    </div>
    <a-modal :visible="previewVisible" :footer="null" @cancel="handleCancel">
      <img alt="example" style="width: 100%" :src="previewImage" />
    </a-modal>
  </div>
</template>

<script>
import NewsTopImg from '@/components/newsTopImg'
import { Pagination, Spin, Icon, Row, Col, Modal} from 'ant-design-vue'
import Item from './component/corporateItem'
import { getCompanyIntro } from '@/js/api/homePage.js'
export default {
  name: 'corporate',
  data () {
    return {
      list: [],
      showList: true,
      title: '',
      current: 1,
      total: 1,
      pageSize: 6,
      detail: {},
      isLoading: false,
      immediate: true,
      colSpan: 6,
      previewVisible: false,
      previewImage: '',
      timer: null
    }
  },
  components: {
    NewsTopImg,
    [Pagination.name]: Pagination,
    [Spin.name]: Spin,
    [Icon.name]: Icon,
    [Row.name]: Row,
    [Col.name]: Col,
    [Modal.name]: Modal,
    Item
  },
  methods: {
    getList () {
      getCompanyIntro({}, res => {
        this.list = res.data.list.map((item) => {
          let url = item.image.url.indexOf('http') > -1 ? item.image.url : 'https://dev-api.jjh9999.com/ruiqi/images/1587960927037.jpg'
          return {
            ...item,
            image: {
              ...item.image,
              url
            }
          }
        })
      })
    },
    setCorPorate () {
      // 15
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        // let width = document.documentElement.clientWidth
        this.setCol()
      }, 10)
    },
    setCol () {
      let width = document.documentElement.clientWidth + 15
      if (width > 1280) {
        this.colSpan = 6
      }  else if (width >= 1024) {
        this.colSpan = 8
      } else if ( width < 1024 && width >= 568) {
        this.colSpan = 12
      } else if (width < 568) {
        this.colSpan = 24
      }
    },
    handleItem (item) {
      this.previewVisible = true
      this.previewImage = item.image.url
    },
    handleCancel () {
      this.previewVisible = false
    }
  },
  created () {
    this.getList()
    window.addEventListener('resize', this.setCorPorate)
  },
  mounted () {
    if (this.immediate) {
      this.immediate = false
      this.setCol()
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.setCorPorate)
  },
}
</script>

