<template>
  <div class="news web-news">
    <div class="loading" v-if="isLoading">
      <a-spin>
        <a-icon slot="indicator" type="loading" style="font-size: 24px; color: #dab866" spin />
      </a-spin>
    </div>
    <div v-else>
      <news-top-img :mainBg="mainBg" />
      <div class="title">{{title}}</div>
      <div class="list-wrapper" v-if="showList">
        <div v-if="list.length">
          <news-list-item
            v-for="(item, index) in list"
            :key="index"
            :msg="item"
            @handleNewItem="handleNewItem"
          />
          <div class="page-wrapper">
            <a-pagination
              v-model="current"
              :total="total"
              :pageSize="pageSize"
              showLessItems
              @change="handlePageChange"
            />
          </div>
        </div>
        <div class="no-content" v-else></div>
      </div>
      <div v-else>
        <Artical :detail="detail" />
      </div>
    </div>
  </div>
</template>

<script>
import newsBg from '@/assets/newsBg.jpg'
import NewsTopImg from '@/components/newsTopImg'
import NewsListItem from './component/newsListItem'
import { Pagination, Spin, Icon } from 'ant-design-vue'
import Artical from './component/artical'
import { getNewsList } from '@/js/api/homePage.js'
export default {
  name: 'news',
  props: {
    msg: String
  },
  data () {
    return {
      newsBg,
      list: [],
      showList: true,
      title: '',
      current: 1,
      total: 1,
      pageSize: 6,
      detail: {},
      isLoading: true,
      mainBg: ''
    }
  },
  components: {
    NewsTopImg,
    NewsListItem,
    [Pagination.name]: Pagination,
    [Spin.name]: Spin,
    [Icon.name]: Icon,
    Artical
  },
  methods: {
    handleNewItem (id) {
      this.$router.push(`/newsDetail/${id}`)
      this
    },
    handlePageChange (page) {
      this.current = page
      this.getList(true)
    },
    getList (isSetPage) {
      let id = this.$route.params && this.$route.params.id
      getNewsList({
        groupId: id,
        page: this.current,
        pageSize: this.pageSize
      }, res => {
        this.isLoading = false
        // list1typenormal
        if (res.data.list && res.data.list.length === 1 && res.data.list[0].type !== 'normal') {
          this.showList = false
          this.detail = res.data.list[0]
          this.mainBg = this.detail.mainImgUrlList[0]
        } else {
          this.showList = true
          this.list = res.data.list
          this.mainBg = this.list[0].mainImgUrlList[0]
          this.total = res.data.count
          if (isSetPage) {
            document.body.scrollIntoView()
          }
        }
        this.title = res.data.group.name
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

